import React, { useState, useRef, useEffect } from 'react';
import './Header.scss';
import {Link} from 'react-router-dom';
import {GoSearch, GoThreeBars} from 'react-icons/go';
import {AiOutlineCloseCircle,AiOutlineHeart} from 'react-icons/ai';
import {TopNavData} from './TopNavData';
import {useSelector, useDispatch} from 'react-redux';
import {RiMenu2Fill,RiArrowDownSLine} from 'react-icons/ri';
import { SignOut } from '../store/Actions/UserActions';
import {Badge, Typography} from '@material-ui/core';
import {TiShoppingCart} from 'react-icons/ti';
import {HiOutlineSearch} from 'react-icons/hi';
import {VscClose} from 'react-icons/vsc';
import {Fade} from 'react-reveal';
import formatCurrency from '../Currency';


function Header(props) {
    
     const wrapperRef = useRef(null);

     const useOutSideClickHandle=(ref)=>{
        useEffect(()=>{
            const handleOutSideClick=(event)=>{
                if(ref.current && !ref.current.contains(event.target)){
                    setprofile(false);
                }
            }
            document.addEventListener("mousedown", handleOutSideClick);
        return () => {
            // Unbind the event listener on clean up
            document.removeEventListener("mousedown", handleOutSideClick);
        };
         },[ref]);
     }

     useOutSideClickHandle(wrapperRef);



    const [profile, setprofile] = useState(false);

    const showprofile=()=> setprofile(!profile);
    const closeprofile=()=> setprofile(false);

    const showsidebar = ()=> props.setsidebar(!props.sidebar);

    const dispatch = useDispatch();

    const Cart = useSelector(state=> state.Cart);
    const {CartItems}= Cart;
    
    const UserSign = useSelector(state=> state.UserSign);
    const {UserInfo}= UserSign;

    const [searchbar, setsearchbar] = useState(false);
    const showsearchbar=()=> setsearchbar(!searchbar);
    const closesearchbar=()=> setsearchbar(false);

    
    const SignOutHandler=()=>{
        dispatch(SignOut());
        closeprofile();
    }

    return (
       
        <div >
            <div className='main-top-header-container'>
                <div style={{display: 'flex', alignItems: 'center', gap: "1rem",marginLeft: "1rem"}}>
                    <Link className='header-links' to='/'>About Us</Link>
                    <Link className='header-links' to='/'>Contact Us</Link>
                </div>
                {UserInfo ? 
                (<div style={{display: 'flex', alignItems: 'center', gap: "1rem", marginRight:'1rem'}}><Link onClick={showprofile} className='header-links'><span>My Account</span><RiArrowDownSLine className='menu-arrow'/></Link></div>) : (<div style={{display: 'flex', alignItems: 'center', gap: "10px", marginRight:'1rem'}}>
                    <Link className='header-links' to='/login'>Login</Link>
                    <Link className='header-links' to='/register'>Sign Up</Link>
                </div>)}
            </div>
           <Fade bottom>
           {!searchbar &&  <div className='top-header-container'>
            <div className='logo-container'>
               <span><Link className='site-logo' to='/'>snmart</Link></span>
            </div>

            <div className='search-bar-container'>
                <input className='main-search-bar' placeholder='Seach Over 10,000 + Products'/>
                <button className='btn-search'><HiOutlineSearch style={{fontSize: '1.25rem'}}/></button>
            </div>

            <div className='signin-cart-container'>
            <GoSearch onClick={showsearchbar} className='search-icon'/>
            <AiOutlineHeart className='cart-icon'/>
            <Link to='/cart' style={{textDecoration: 'none'}}><div className='shopping-cart'>
                <TiShoppingCart className='cart-icon'/>
                <span>{CartItems.length > 0 && (`${CartItems.length}${" "}Item(s) :`)}</span>
                <span>{formatCurrency(CartItems.reduce((a,c)=> a + c.Price * c.qty, 0))}</span>
            </div></Link>

            </div>
            </div>}
           </Fade>

            <Fade top>
            <div className={searchbar ? 'show-searchbar search-active': 'show-searchbar'}>
            <div style={{display: 'flex', alignItems: 'center', width: "100%", justifyContent: "space-between"}}>
            <input className='main-search-bar' placeholder='Seach Over 10,000 + Products'/>
            <button className='btn-search'><HiOutlineSearch style={{fontSize: '1.25rem'}}/></button>
            </div>
            <VscClose className='close-icon' onClick={closesearchbar}/>
            </div>
            </Fade>

            <div className='second-header-container'>
                <div className='menu-bar-container' onClick={showsidebar}>
                {props.sidebar ?  <AiOutlineCloseCircle className='menu-bar'/> :
                <RiMenu2Fill className='menu-bar'/>}
                </div>

                <div className='top-nav-data-container'>
                {TopNavData.map((item)=>(
                    <Typography className='navi-item' component={Link} to={item.Path}>
                        <span className='nav-title'>{item.Title}</span>
                    </Typography>
                ))}
                </div>


            </div>

            {/* <div className='top-divider'>
            <marquee width="60%" direction="left" width="100%">10% Discounts for All Products Above Rs 2500 
            {" / "}20% Discounts for All Products Above Rs 5000</marquee>
            </div> */}
            {
                <div ref={wrapperRef} className={profile ? 'show-profile profile-active' : 'show-profile'}>
                    <div>Profile</div>
                    <div>Order History</div>
                    <div onClick={SignOutHandler}>Logout</div>
                </div>
            }



            
            
          </div>
    )
}

export default Header;
